import React, { FC, useLayoutEffect, useRef, useState } from "react";
import clsx from "clsx";

import { Button as ButtonComponent, useSoundSystemControl } from "components";

import {
  outlinesImages,
  TableListValues,
  towerImages,
  useAutoCells,
  useDiff,
  useGameStatus,
  useIsAutoOpen,
  useTowerControl,
} from "pages/Tower/controller";

import BadUrl from "./assets/bad.svg";
import GoodUrl from "./assets/good.svg";
import css from "./style.module.scss";
import {
  HotKeysCallbacks,
  useHotKeyListener,
} from "components/HotKeys/Listener";

interface Props {
  highLight?: boolean;
  isActive: boolean;
  number: number;
  system?: TableListValues;
  setLoad: () => void;
  isLoad: boolean;
  rowNumber: number;
}

export const Button: FC<Props> = ({
  highLight: isHighLight,
  isActive,
  number,
  system,
  setLoad,
  isLoad,
  rowNumber,
}) => {
  const { manager } = useTowerControl();
  const { isOpen } = useIsAutoOpen();
  const { cells, setCells } = useAutoCells();
  const { diff } = useDiff();
  const { status } = useGameStatus();
  const { controlFunctions } = useSoundSystemControl();

  const handleClick = () => {
    controlFunctions.playEffect("fieldClick");

    if (isOpen) {
      setCells({
        cell: number,
        row: rowNumber,
      });
    } else {
      setLoad();
      manager.sweep(number);
    }
  };

  const isBad = system?.status === "bad";
  const isAutoSelected = isOpen && cells[rowNumber] === number;

  const getImageLink = () => {
    const SelectedImage = towerImages[diff];
    const buttonStatus = system?.status;

    switch (buttonStatus) {
      case "bad":
        return status !== "IN_PROGRESS" ? (
          <img src={BadUrl} alt="loss" />
        ) : (
          <outlinesImages.vomit />
        );

      case "good":
        return <img src={GoodUrl} alt="win" />;

      default:
        break;
    }

    return <SelectedImage />;
  };

  const getHotKys = () => {
    const callBacks: HotKeysCallbacks = {};

    if (isActive)
      switch (number) {
        case 0:
          callBacks.callbackKeyQ = () => handleClick();
          break;
        case 1:
          callBacks.callbackKeyW = () => handleClick();
          break;
        case 2:
          callBacks.callbackKeyE = () => handleClick();
          break;
        case 3:
          callBacks.callbackKeyR = () => handleClick();
          break;

        default:
          break;
      }

    return callBacks;
  };

  useHotKeyListener(getHotKys());

  return (
    <ButtonComponent
      onClick={handleClick}
      transparent={isHighLight}
      className={clsx(
        css.pawButton,
        ((!isLoad && isActive) || isOpen) && css.pointer,
        isActive && css.active,
        isBad && css.bad,
        isLoad && css.load,
        isAutoSelected && css.selected,
      )}>
      {getImageLink()}
    </ButtonComponent>
  );
};
