import React, { memo } from "react";

import { Options, Statistics as DefaultStats } from "components";

import { MoreHotKeys } from "components/HotKeys/Modal/types";

import { Controller, Game, History } from "./components";
import { TowerControlProvider } from "./controller";
import css from "./style.module.scss";

export interface RocketStartType {
  number: boolean;
  animation: boolean;
}

const Statistics = memo(DefaultStats);

const towerHotKeys: MoreHotKeys[] = [
  { button: "Q", formatHotKeyButtonId: "towers.hotkeys.q" },
  { button: "W", formatHotKeyButtonId: "towers.hotkeys.w" },
  { button: "E", formatHotKeyButtonId: "towers.hotkeys.e" },
  { button: "R", formatHotKeyButtonId: "towers.hotkeys.r" },
];

const TowerPage = () => {
  return (
    <div className={css.root}>
      <TowerControlProvider>
        <div className={css.item}>
          <History />
          <div className={css.gameBlock}>
            <div className={css.gameCell}>
              <div className={css.gameWrapper}>
                <Game />
              </div>
              <div className={css.gameFooter}>
                <Options game="towers" moreHotKeys={towerHotKeys} />
              </div>
            </div>
          </div>
          <Controller />
        </div>
      </TowerControlProvider>
      <div className={css.item}>
        <div className={css.statisticsWrapper}>
          <Statistics withFairness />
        </div>
      </div>
    </div>
  );
};

export default TowerPage;
