import React from "react";
import clsx from "clsx";

import { useGameStatus, useIsAutoStarted } from "pages/Tower/controller";
import { BetInput, Selector } from "./components";
import css from "./style.module.scss";

export const Inputs = () => {
  const { isStarted } = useIsAutoStarted();
  const { status } = useGameStatus();

  return (
    <div
      className={clsx(
        css.root,
        ((!isStarted && status === "IN_PROGRESS") || isStarted) && css.disable,
      )}>
      <BetInput />
      <Selector />
    </div>
  );
};
