import React from "react";

import { AutoDecision } from "components";

import { AutoDecisions } from "components/AutoDecision/type";
import { useDecision } from "pages/Tower/controller";
import { Limits } from "./Limits";
import css from "./style.module.scss";

const Control = () => {
  const { decision, setDecision } = useDecision();

  const createHandleDecision = (isWin: boolean) => {
    return (value: AutoDecisions) => {
      const { betPercent, variant } = value;
      setDecision({
        betPercent,
        variant,
        isWin,
      });
    };
  };

  return (
    <>
      <div className={css.root}>
        <AutoDecision
          onChange={createHandleDecision(true)}
          title="on win"
          value={decision.win}
          withoutMultiplier
          classes={{
            input: css.input,
          }}
        />

        <AutoDecision
          onChange={createHandleDecision(false)}
          title="on loss"
          value={decision.loss}
          withoutMultiplier
          classes={{
            input: css.input,
          }}
        />
      </div>
      <Limits />
    </>
  );
};

export default Control;
