import React from "react";
import clsx from "clsx";

import { Select, SelectorProps } from "components";
import { repeat } from "utils";

import { useDiff, Diff, towerImages } from "pages/Tower";
import { items } from "./assets";
import css from "./style.module.scss";

const getOptionComponent = (value: Diff) => {
  try {
    const Component = towerImages[value];

    return <Component className={css.diffImage} />;
  } catch {
    return null;
  }
};

const createAdornment = (goodItems: number, badItems: number) => {
  return (
    <div
      style={{
        display: "flex",
      }}>
      {repeat(goodItems + badItems, (i) => (
        <img
          alt="item"
          className={css.item}
          src={i < badItems ? items.bad : items.good}
          key={`items_${goodItems + badItems}_${i}`}
        />
      ))}
    </div>
  );
};

const difficulty: SelectorProps["options"] = [
  {
    value: "easy",
    label: "Easy",
    name: "Difficulty",
    icon: getOptionComponent("easy"),
    labelAdornment: createAdornment(3, 1),
  },
  {
    value: "medium",
    label: "Medium",
    name: "Difficulty",
    icon: getOptionComponent("medium"),
    labelAdornment: createAdornment(2, 1),
  },
  {
    value: "hard",
    label: "Hard",
    name: "Difficulty",
    icon: getOptionComponent("hard"),
    labelAdornment: createAdornment(1, 1),
  },
  {
    value: "brutal",
    label: "Brutal",
    name: "Difficulty",
    icon: getOptionComponent("brutal"),
    labelAdornment: createAdornment(1, 3),
  },
  {
    value: "wicked",
    label: "Wicked",
    name: "Difficulty",
    icon: getOptionComponent("wicked"),
    labelAdornment: createAdornment(1, 2),
  },
];

export const Selector = () => {
  const { diff, setDiff } = useDiff();

  return (
    <Select
      value={diff}
      className={css.root}
      labelClass={clsx(css.itemTitle, css.text)}
      classes={{
        selectedAmount: clsx(css.selectedItem, css.text),
        expandIcon: css.arrow,
      }}
      itemClass={css.selectItem}
      onChange={(value) => setDiff(value as Diff)}
      options={difficulty}
      isCoinSelect={false}
    />
  );
};
