import React, { Suspense } from "react";

import { ProgressFallback } from "components";

import LazyComponentLoader from "config/LazyComponentLoader";
import { useIsAutoOpen } from "pages/Tower";
import { Panel } from "./components";
import css from "./style.module.scss";

const Auto = React.lazy(() =>
  LazyComponentLoader(import("./components/Control")),
);

export const Controller = () => {
  const { isOpen } = useIsAutoOpen();

  return (
    <div className={css.root}>
      <Panel />
      {isOpen && (
        <Suspense fallback={<ProgressFallback />}>
          <Auto />
        </Suspense>
      )}
    </div>
  );
};
