import React, { FC, Fragment } from "react";
import clsx from "clsx";

import { useModalLocation } from "hooks";
import { useUser } from "providers";
import css from "./style.module.scss";

interface Props {
  value: string | number;
  id: string | number;
}

export const Element: FC<Props> = ({ value, id }) => {
  const { user } = useUser();
  const { pushHistoryTo } = useModalLocation("bet-data");

  const handleClick = () => {
    pushHistoryTo({
      searchParams: {
        id: `${id}`,
        userName: user?.user_name || "",
      },
    });
  };

  const payout = Number(value);
  return (
    <Fragment key={id}>
      <div
        className={clsx(css.element, !payout && css.loss)}
        onClick={handleClick}>
        <span>{payout.toFixed(5).slice(0, -3)}X</span>
      </div>
    </Fragment>
  );
};
