import React, { Fragment } from "react";

import { repeat } from "utils";
import {
  useDiff,
  useGameStatus,
  useGameTable,
  useTowerControl,
} from "pages/Tower/controller";
import { Row } from "./components";
import css from "./style.module.scss";

const rowsAmount = 9,
  highLightPeriod = 3;

export const Table = () => {
  const { manager } = useTowerControl();

  const { list, selectedRow } = useGameTable();
  const { status } = useGameStatus();
  const { diff } = useDiff();
  const cellsAmount = manager.fields.cellsAmount[diff] as 2 | 3 | 4;

  return (
    <div className={css.root}>
      {repeat(rowsAmount, (i) => {
        const index = rowsAmount - i;

        return (
          <Fragment key={`rowKey_#${i}`}>
            <Row
              isActive={
                (selectedRow === index - 1 && status === "IN_PROGRESS") ||
                (index === 1 && !selectedRow && status === "IN_PROGRESS")
              }
              system={list[index - 1]}
              rowNumber={index - 1}
              cellsAmount={cellsAmount}
              highLight={i % highLightPeriod === 0}
            />
          </Fragment>
        );
      })}
    </div>
  );
};
