import React, { useEffect, useState } from "react";

import { coinsManager, useCoins } from "hooks";
import { useTowerControl, useBetAmount } from "pages/Tower";

export const InUsd = () => {
  const { manager } = useTowerControl();
  const { selectedCoin } = useCoins();
  const [usdPriceValue, setUsdPriceValue] = useState(
    manager.fields.general.amount * Number(selectedCoin?.price || 0),
  );

  useBetAmount((value) => {
    const { price = 1 } = coinsManager.selectedCoin || {};
    setUsdPriceValue(Number(price) * value);
  });

  useEffect(() => {
    setUsdPriceValue(
      manager.fields.general.amount * Number(selectedCoin?.price || 0),
    );
  }, [selectedCoin?.coin]);

  return <span>{usdPriceValue.toFixed(8)} USD</span>;
};
