import React, { useRef, useState } from "react";

import { useTowerControl, useSessionCoin, useBetAmount } from "pages/Tower";
import { useHotKeyListener } from "components/HotKeys/Listener";

import { Button, GetCoinImg, Input, InputAdornment, Warning } from "components";
import { coinsManager } from "hooks";
import { rangeBetween } from "utils";

import { InUsd } from "./InUsd";
import css from "./style.module.scss";

export const BetInput = () => {
  const { manager } = useTowerControl();
  const input = useRef<HTMLInputElement | null>(null);
  const [warning, setWarning] = useState(false);
  const coin = useSessionCoin();

  const { setBetAmount: setBet } = useBetAmount((value) => {
    if (input.current) input.current.value = value.toFixed(8);
  });

  const setBetAmount = (value: string | number) => {
    const { coin = "GEM" } = coinsManager.selectedCoin || {};
    const newValue = rangeBetween(
      Number(Number(value).toFixed(8)),
      manager.fields.config?.limits[coin].minimum_bet || 0,
      manager.fields.config?.limits[coin].maximum_bet || 1,
    );

    setBet(newValue);
  };

  const handleButton = (type: "min" | "max" | "x2" | "/2") => {
    const { coin = "GEM", balance } = coinsManager.selectedCoin || {};
    const { general, config, auto } = manager.fields;
    const { minimum_bet, maximum_bet } = config?.limits[coin] || {};
    const { amount } = general;
    let value = amount;

    const actualBalance = Number(balance);

    if (auto.sys.isStarted) {
      return;
    }

    switch (type) {
      case "min":
        value = minimum_bet || 0.001;
        break;
      case "max":
        value = Math.min(maximum_bet || actualBalance || 1, actualBalance);
        break;
      case "x2":
        value *= 2;
        break;
      case "/2":
        value /= 2;
        break;
      default:
        break;
    }
    setBetAmount(value);
  };

  useHotKeyListener({
    callbackKeyS: () => handleButton("x2"),
    callbackKeyA: () => handleButton("/2"),
  });

  const buttonsConfig = [
    {
      name: "min",
      onClick: () => handleButton("min"),
    },
    {
      name: "/2",
      onClick: () => handleButton("/2"),
    },
    {
      name: "x2",
      onClick: () => handleButton("x2"),
    },
    {
      name: "max",
      onClick: () => setWarning(true),
    },
  ];

  return (
    <div className={css.betInput}>
      <div className={css.titleWrapper}>
        <span>Amount</span>
        <InUsd />
      </div>

      <Warning
        isOpen={warning}
        onClose={() => setWarning(false)}
        callBack={() => handleButton("max")}
      />

      <Input
        ref={input}
        className={css.input}
        rootClass={css.inputRoot}
        labelClassName={css.label}
        classes={{ input: css.inputElement }}
        onBlur={({ target }) => setBetAmount(target.value)}
        label="Amount"
        endAdornment={
          <InputAdornment position="end" className={css.adornment}>
            <div className={css.buttons}>
              {buttonsConfig.map(({ name, onClick }) => (
                <Button className={css.button} onClick={onClick}>
                  {name}
                </Button>
              ))}
            </div>
          </InputAdornment>
        }
        startAdornment={
          <InputAdornment position="start">
            <GetCoinImg coin={coin} staticCoin />
          </InputAdornment>
        }
      />
    </div>
  );
};
