import React, { memo, useEffect, useRef } from "react";

import { AutoLimits, Inputs } from "components";
import {
  Elements,
  useIsAutoStarted,
  useTowerControl,
} from "pages/Tower/controller";

const Component = () => {
  const { manager } = useTowerControl();
  const { isStarted } = useIsAutoStarted();
  const autoInputs = useRef<Inputs>({
    numberOfBets: null,
    stopOnLoss: null,
    stopOnProfit: null,
  });

  useEffect(() => {
    const unListen = manager.listen({
      element: Elements.limits,
      callBack: (value) => {
        if (autoInputs.current)
          Object.entries(value).forEach((data) => {
            const [key, value] = data as [keyof Inputs, number];
            const input = autoInputs.current[key];

            if (input) input.value = `${value}`;
          });
      },
    });

    manager.setValue({
      element: Elements.limits,
      value: {
        numberOfBets: 0,
        stopOnLoss: 0,
        stopOnProfit: 0,
      },
    });
    return () => {
      unListen();
    };
  }, []);

  return (
    <AutoLimits
      setValue={(limits) => {
        manager.setValue({
          element: Elements.limits,
          value: {
            ...manager.fields.auto.fields.limits,
            ...limits,
          },
        });
      }}
      ref={autoInputs}
      disable={isStarted}
    />
  );
};

export const Limits = memo(Component);
