import { useEffect } from "react";
import { isKeyboardEvent } from "utils/guards";
import { useHotKeys } from "../Provider";

enum ActiveKeys {
  KeyA,
  KeyS,
  Space,
  KeyQ,
  KeyW,
  KeyE,
  KeyR,
  KeyT,
  KeyY,
  KeyD,
}

export interface HotKeysCallbacks {
  callbackKeyA?: () => void;
  callbackKeyS?: () => void;
  callbackSpace?: () => void;
  callbackKeyQ?: () => void;
  callbackKeyW?: () => void;
  callbackKeyE?: () => void;
  callbackKeyR?: () => void;
  callbackKeyT?: () => void;
  callbackKeyY?: () => void;
  callbackKeyD?: () => void;
}

export const useHotKeyListener = (callbacks: HotKeysCallbacks) => {
  const { isHotkeyActive, isHotKeysPaused } = useHotKeys();

  useEffect(() => {
    const keyDownListenerIgnore = (event: Event | KeyboardEvent) => {
      if (
        !isKeyboardEvent(event) ||
        !isHotkeyActive ||
        isHotKeysPaused ||
        //@ts-ignore
        event.target.tagName.toLowerCase() !== "body"
      ) {
        return;
      }
      const key = event.code;
      if (key in ActiveKeys) {
        event.preventDefault();
      }
    };

    const keyListener = (event: Event | KeyboardEvent) => {
      if (
        !isKeyboardEvent(event) ||
        !isHotkeyActive ||
        isHotKeysPaused ||
        //@ts-ignore
        event.target.tagName.toLowerCase() !== "body"
      ) {
        return;
      }

      const key = event.code;
      if (key in ActiveKeys) {
        event.preventDefault();
        const {
          callbackKeyA,
          callbackKeyS,
          callbackSpace,
          callbackKeyQ,
          callbackKeyW,
          callbackKeyE,
          callbackKeyR,
          callbackKeyT,
          callbackKeyY,
          callbackKeyD,
        } = callbacks;
        switch (key) {
          case "KeyA":
            if (callbackKeyA) callbackKeyA();
            break;
          case "KeyS":
            if (callbackKeyS) callbackKeyS();
            break;
          case "Space":
            if (callbackSpace) callbackSpace();
            break;
          case "KeyQ":
            if (callbackKeyQ) callbackKeyQ();
            break;
          case "KeyW":
            if (callbackKeyW) callbackKeyW();
            break;
          case "KeyE":
            if (callbackKeyE) callbackKeyE();
            break;
          case "KeyR":
            if (callbackKeyR) callbackKeyR();
            break;
          case "KeyT":
            if (callbackKeyT) callbackKeyT();
            break;
          case "KeyY":
            if (callbackKeyY) callbackKeyY();
            break;
          case "KeyD":
            if (callbackKeyD) callbackKeyD();
            break;
          default:
            break;
        }
      }
    };

    window.addEventListener("keydown", keyDownListenerIgnore);
    window.addEventListener("keyup", keyListener);
    return () => {
      window.removeEventListener("keydown", keyDownListenerIgnore);
      window.removeEventListener("keyup", keyListener);
    };
  }, [isHotkeyActive, isHotKeysPaused, callbacks]);
};
