import React, { useEffect, useState } from "react";
import clsx from "clsx";

import { createTimeouts } from "utils";

import { Elements, useTowerControl } from "pages/Tower/controller";
import { Element } from "./Element";
import css from "./style.module.scss";

export const History = () => {
  const { manager } = useTowerControl();
  const [history, setHistory] = useState(manager.fields.general.history);
  const [animation, setAnimation] = useState(false);

  useEffect(() => {
    const unListen = manager.listen({
      element: Elements.history,
      callBack: (history) => {
        setHistory(history);
        setAnimation(true);
      },
    });

    return () => {
      unListen();
    };
  }, []);

  useEffect(() => {
    const { clearTimeouts, pushTimeout } = createTimeouts();

    if (animation)
      pushTimeout(() => {
        setAnimation(false);
      }, 100);

    return () => {
      clearTimeouts();
    };
  }, [animation]);

  return (
    <div className={clsx(css.historyWrapper, animation && css.lastAnimated)}>
      {history.map(({ id, value }) => {
        return <Element value={value} id={id} />;
      })}
    </div>
  );
};
