import React from "react";

import { Button, GetCoinImg, SwitchBox } from "components";
import { useGetLocale } from "providers";
import { useLocalStorage } from "hooks";

import { useHotKeyListener } from "components/HotKeys/Listener";
import {
  useIsAutoOpen,
  useAutoCells,
  useTowerControl,
  useGameTable,
  useGameStatus,
  useIsAutoStarted,
  useGetProfit,
  useSessionCoin,
} from "pages/Tower";
import css from "./style.module.scss";

export const Bet = () => {
  const intl = useGetLocale();
  const [token] = useLocalStorage("token");
  const { manager } = useTowerControl();

  const { isOpen, setIsOpen } = useIsAutoOpen();
  const { isStarted } = useIsAutoStarted();
  const { selectedRow } = useGameTable();
  const { status } = useGameStatus();

  const { cells } = useAutoCells();
  const profit = useGetProfit();
  const coin = useSessionCoin();

  const handleClick = async () => manager.onClick();

  const isDisable = () => {
    if (
      !token ||
      status === null ||
      (status === "IN_PROGRESS" && !selectedRow && !isStarted)
    )
      return true;
    if (!isOpen || isStarted) return false;

    const trimmedCells = [...cells];
    while (
      trimmedCells.length &&
      trimmedCells[trimmedCells.length - 1] === undefined
    ) {
      trimmedCells.pop();
    }

    const isDisable =
      trimmedCells.findIndex((value) => value === undefined) !== -1;

    if (!trimmedCells.length) return true;
    return isDisable;
  };

  const disableButton = isDisable();

  const getButtonText = () => {
    if (isOpen) {
      if (isStarted) {
        return {
          top: "stop auto",
        };
      } else {
        return {
          top: "start auto",
        };
      }
    }

    switch (status) {
      case "IN_PROGRESS":
        return {
          top: "CASHOUT",
          bottom: disableButton ? null : profit,
        };

      default:
        return {
          top: "BET",
        };
    }
  };

  useHotKeyListener({
    callbackSpace: () => {
      if (!isDisable()) handleClick();
    },
  });

  const buttonText = getButtonText();

  return (
    <div className={css.root}>
      <Button
        onClick={handleClick}
        className={css.betButton}
        disabled={disableButton}>
        <span>{buttonText.top}</span>

        {!!buttonText.bottom && (
          <div className={css.bottomButtonText}>
            <GetCoinImg coin={coin} staticCoin />
            {buttonText.bottom}
          </div>
        )}
      </Button>

      <SwitchBox
        isChecked={isOpen}
        switchHandleChange={(is) => setIsOpen(is)}
        text={{
          off: intl.formatMessage({ id: "crashInterface.bet.switch.manual" }),
          on: intl.formatMessage({ id: "crashInterface.bet.switch.auto" }),
        }}
        classes={{
          root: status === "IN_PROGRESS" && !isStarted ? css.disable : "",
        }}
        className={css.switch}
      />
    </div>
  );
};
