import React, { FC, Fragment, useEffect, useState } from "react";

import { GetCoinImg } from "components";
import { repeat } from "utils";

import {
  TableListValues,
  useBetAmount,
  useDiff,
  useGetProfit,
  useSessionCoin,
  useTowerControl,
} from "pages/Tower/controller";
import { Button } from "../Button";
import css from "./style.module.scss";

interface Props {
  cellsAmount: 2 | 3 | 4;
  highLight?: boolean;
  isActive: boolean;
  system?: TableListValues[];
  rowNumber: number;
}

export const Row: FC<Props> = ({
  cellsAmount,
  highLight,
  rowNumber,
  isActive,
  system,
}) => {
  const coin = useSessionCoin();
  const [isLoad, setIsLoad] = useState(false);
  const profit = useGetProfit(rowNumber);

  useEffect(() => {
    if (!isActive) {
      setIsLoad(false);
    }
  }, [isActive]);

  const setLoad = () => setIsLoad(true);

  return (
    <div className={css.root}>
      {repeat(cellsAmount, (i) => {
        const buttonSysValue = system?.find(({ value }) => value === i);
        return (
          <Fragment key={`rowKey_#${i}_Amount${cellsAmount}`}>
            <Button
              highLight={highLight}
              isActive={isActive}
              number={i}
              system={buttonSysValue}
              setLoad={setLoad}
              isLoad={isLoad}
              rowNumber={rowNumber}
            />
          </Fragment>
        );
      })}

      <div className={css.winPrice}>
        <GetCoinImg className={css.coinImg} coin={coin} />
        {profit}
      </div>
    </div>
  );
};
